$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-events {
  .upRecentEventsContainerInner {
    padding: 1rem;
    background-color: $blue;
    border-radius: 5px;
    background-size: cover;
    background-position: center;
    h2 {
      color: $white;
    }
    .upFeedItem {
      margin-top: 15px;
      border-radius: 5px;
      overflow: hidden;
      background: #f2f2f2;
      position: relative;
      h4 {
        a {
          color: $blue;
          &:hover, &:visited {
            color: $blue;
            text-decoration: none;
          }
        }
      }
      .newEventImage {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        background-size: cover;
        background-position: center;
        .newEventImageLink {
          position: absolute;
          top: 0; bottom: 0; right: 0; left: 0;
        }
      }
      .newEventText {
        padding: 15px;
      }
      p {
        margin-bottom: 0.25rem;
        a { display: inline-block; }
      }
      .postTime {
        font-style: italic;
      }
      .postReadMore a {
        font-weight: bold;
        color: $yellow;
        margin-bottom: 0;
      }
    }
  }
  #pastEvents {
    .pastEventContainer {
      position: relative;
      overflow: hidden;
      background-color: #f2f2f2;
      border-radius: 5px;
      margin-bottom: 15px;
      padding-top: 20rem;
      background-size: cover;
      background-position: center;
      box-shadow: none;
      transition: all .2s ease-in-out;
      &:hover {
        box-shadow: 0 0 25px -5px #000;
      }
      h3 {
        font-size: 1rem;
      }
      .pastEventTeaser {
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        padding: 1rem;
        background: $blue;
        color: $white;
      }
      .pastEventDate {
        text-transform: uppercase;
        color: $yellow;
        font-size: 0.75rem;
        margin-bottom: 0;
      }
    }
  }
}

.single-events {
  #eventHeader {
    background-size: cover;
    background-position: center;
    padding-top: 8rem;
    padding-bottom: 8rem;
    position: relative;
    .cover {
      position: absolute;
      top: 0; left: 0; bottom: 0; right: 0;
      background-color: rgba(0,0,0,0.6);
      z-index: 1;
    }
    .eventHeaderText {
      position: relative;
      z-index: 2;
      h1, p {
        margin-top: 0;
        margin-bottom: 0;
        color: $white;
      }
      p.bookNow {
        margin-top: 1rem;
      }
    }
  }
  .memberContainer {
    padding-bottom: 2rem;
  }
  .eventItemImgContainer {
    position: relative;
    .eventItemGallery {
      position: relative;
      .slick-arrow {
        position: absolute;
        z-index: 1;
        top: 43%;
        padding: 15px;
        background: rgba(0,0,0,0.4);
        color: #fff;
        opacity: 0.6;
        &:hover {
          opacity: 1;
          cursor: pointer;
        }
      }
      .slick-prev {
        left: 0;
      }
      .slick-next {
        right: 0;
      }
      .eventItemGalleryImg {
        background-size: cover;
        background-position: center;
        height: 350px;
      }
    }
    .eventItemGalleryNav {
      margin-top: 5px;
      img {
        margin: 5px;
      }
      .slick-arrow {
        position: absolute;
        z-index: 1;
        top: 40%;
        padding: 5px;
        color: #000;
        opacity: 0.2;
        &:hover {
          opacity: 1;
          cursor: pointer;
        }
      }
      .slick-prev {
        left: -20px;
      }
      .slick-next {
        right: -20px;
      }
    }
  }
  .eventQuickInfo {
    margin-top: 40px;
    border: 3px dotted $blue;
    padding: 15px;
    border-radius: 15px;
    .eventQuickInfoInner {
      .eventInfoList {
        margin-left: 0;
        padding-left: 0;
        margin-bottom: 0;
        list-style: none;
        .eventInfoItem {
          margin-left: 0;
          padding-left: 0;
          font-size: 0.8rem;
        }
      }
    }
  }
  .eventMap {
    margin-top: 40px;
  }
}